mily: 'Fredericka the Great', sans-serif;\n            weight: bold;\n        }\n        color: $main-light;\n    }\n    \n    #pack-list-btn:hover {\n            background-color: $semi-trans_persian-green;\n    }\n\n    #pack-list-form {\n        box-shadow: none;\n\n        .input#input {\n            margin-left: 1em;\n            max-width: 45%;\n            background-color: $semi-trans_main-light;\n        }\n        \n        .input#input:focus {\n            outline-color: $maize-crayola;\n            outline: {\n                border-radius: 16px;\n            }\n        }\n    }            \n}\n\n.section-container_trip-length {\n    grid-area: tl;\n    background-color: $trans-background_green;\n\n    #trip-length_header-wrapper {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        // background-color: rgba(119, 36, 4, 0.3);\n        background-color: rgba(158, 19, 61, 0.3);\n        width: 100%;\n        // background-color: rgba(245, 245, 245, 0.6);\n        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);\n    }\n\n    #trip-range {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        transform: translateY(06vh);\n        width: 100%;\n        background-color: $trans-background_test-color;\n    }\n\n        #trip-length-list {\n            padding-left: 0;\n            width: 94%;\n\n            ul {\n                padding: 0;\n            }\n\n            li {\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                box-shadow: $parchment-box-shadow;\n                width: 100%;\n            }\n        }\n}\n\n.section-container_header {\n    grid-area: hd;\n    background-color: $main-light;\n    h1 {\n        color: $main-dark;\n    }\n}\n\n.section-container_footer {\n    grid-area: f;\n    background-color: $trans-background_green;\n\n}\n\nbody {\n    font: {\n        family: 'TerraIgnota'